import actionbar from "@/components/actionbar/actionbar.vue"
import securitiesbottom from "@/components/allcoupons/securitiesbottom.vue"
const app = getApp();
//这是全部优惠卷路由
export default{
	data(){
		return {
			statusBar:0,
			items:"未使用",
			TabCur: 0,
			scrollLeft: 0,
			coupons:[
				"未使用",
				"已使用",
				"已过期"
			],
			getchildlistdata:[],
		}
	},
	methods:{
		tabSelect(e) {
			// console.log(e.currentTarget.dataset)
			// console.log(this.coupons[this.TabCur+1])
			let {id,items} = e.currentTarget.dataset
			this.items = items
			this.TabCur = id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		},
		//子组件的数据 在methods定义接收  在生命周期的函数中 负责执行该函数
		getchildlist(e){
			this.getchildlistdata = e;
			//再将新的数组加入到缓存中
			uni.setStorage({
				key:"couponsData",
				data:this.getchildlistdata,
				success:(res)=>{
					
				}
			})
		}
	},
	onLoad(){
		this.statusBar = app.globalData.statusBar
		
	},
	components:{
		securitiesbottom
	},
	updated(){//在数据发生改变的时候接受
		const _this = this;
		this.getchildlist()
	}
}
